<template>

  <div id="register" class="content-page">
    <div class="register-container">
      <div class="page-header">
        <h2>创建新账户</h2>
        <p>加入志愿社区管家平台</p>
      </div>

      <div class="form-content">
        <div class="form-row">
          <!-- 左 -->
          <div class="form-col">
            <!-- 用户名 -->
            <div class="form-group">
              <label for="register-name">用户名</label>
              <input type="text" id="register-name" class="form-control" placeholder="姓名由中文、大小写英语、数字组成的十个字"
                name="username" v-model="name" @input="yhname">
              <span class="error-message" id="username-error">{{ cwname }}</span>
            </div>
            <!-- 密码 -->
            <div class="form-group">
              <label for="register-password">密码</label>
              <input type="password" id="register-password" class="form-control" placeholder="密码由数字、大小写英语组成的十个字符"
                name="Password" v-model="password" @input="yhpassword">
              <span class="error-message" id="password-error">{{ cwpassword }}</span>
            </div>

            <!-- 电子邮件 -->
            <div class="form-group">
              <label for="register-email">电子邮箱</label>
              <input type="text" id="register-email" class="form-control" placeholder="电子邮件由字符: 字母、数字、特殊字符组成"
                name="email" v-model="email" @input="yhemail">
              <span class="error-message" id="email-error">{{ cwemail }}</span>
            </div>
          </div>
          <!-- 右 -->
          <div class="form-col">
            <!-- 手机号 -->
            <div class="form-group">
              <label for="register-phone">手机号码</label>
              <input type="tel" id="register-phone" class="form-control" placeholder="请输入11位手机号码" name="phone"
                v-model="phone" @input="yhphone">
              <span class="error-message" id="phone-error">{{ cwphone }}</span>
            </div>

            <!-- 二次密码 -->
            <div class="form-group">
              <label for="register-confirm-password">确认密码</label>
              <input type="password" id="register-confirm-password" class="form-control" placeholder="请再次输入密码"
                v-model="againpassword" @blur="yhagainpassword">
              <span class="error-message" id="confirm-password-error">{{ cwagainpassword }}</span>
            </div>

            <!-- 验证码 -->
            <div class="form-group">
              <label for="verification-code">验证码</label>
              <div class="verification-group">
                <input type="text" id="verification-code" class="form-control" placeholder="请输入6位验证码"
                  name="verificationCodes" v-model="yanzhengma" @input="shuruyanzhengma()">
                <button :disabled="isDisabled" type="button" style="width: 100px;" class="verification-code-btn"
                  id="verification-btn" @click="btnyanzhengma">
                  {{ textverificationCodes }}
                </button>
              </div>
              <span class="error-message" id="confirm-password-error">{{ cwyanzhengma }}</span>
            </div>
          </div>
        </div>
        <!-- 政策 -->
        <div class="agreement-group">
          <input type="checkbox" id="agree" v-model="tongyi" @click="iftongyi">
          <label for="agree">我已阅读并同意<a @click="xieyi">服务协议</a>和<a @click="xieyi">隐私政策</a></label>
        </div>
        <!-- 注册 -->
        <button :disabled="isDisabled1" class="btn" id="registerBtn" @click="zhuce">{{ annuvalue }}</button>
        <!-- 立即登录 -->
        <div class="login-link">
          <p>已有账号? <a href="#" id="goToLogin" @click="lijidenglu">立即登录</a></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loadXMLDoc from '@/store/AjaxRequest.js';
export default {

  data() {
    return {
      //name
      name: "",
      cwname: '',

      //phone
      phone: "",
      cwphone: '',

      //password
      password: "",
      cwpassword: '',

      //again  password
      againpassword: "",
      cwagainpassword: "",

      //Email
      email: "",
      isemail: false,
      cwemail: '',

      // yanzhengma
      yanzhengma: '',
      cwyanzhengma: '',

      //同意协议
      tongyi: false,

      //按钮
      isDisabled: false,
      isDisabled1: false,
      annuvalue: '注册',

      //客户信息
      userarr: [],

      //验证码文字
      textverificationCodes: "获取验证码"
    };
  },
  methods: {
    //获取验证码
    btnyanzhengma() {
      this.isDisabled = true;
      // 改活3
      let time;
      if (this.isemail) {
        loadXMLDoc({
          reqBody: { "email": this.email },
          reqUrl: "http://8.148.31.153:3030/verificationCodes",
          callback: (error, resData) => {
            try {
              const data = JSON.parse(resData)
              if (!error) {
                //发送成功
                this.$store.commit("tanchaung", "验证码已发送，注意查收")
                this.$store.commit("tanchuangshow", true)
                let num = 60
                time = setInterval(() => {
                  this.textverificationCodes = num
                  if (num == 0) {
                    num = 60
                    clearInterval(time)
                    time = null
                    this.textverificationCodes = "获取验证码"
                    this.isDisabled = false;
                  }
                  num--
                }, 1000);

                //发送失败
                if (!data.success) {
                  this.$store.commit("tanchaung", data.message.msg === "用户已存在" ? "用户已存在" : "发送失败")
                  this.$store.commit("tanchuangshow", true)
                  return
                }
              }
            } catch (error) {
              console.log(error.message)
            }
          }
        })
      } else {
        this.$store.commit("tanchaung", "电子邮件格式不正确")
        this.$store.commit("tanchuangshow", true)
        this.isDisabled = false;
      }
    },

    //注册按钮
    zhuce() {
      this.isDisabled1 = true;
      this.annuvalue = "注册中..."

      if (this.tongyi) {
        loadXMLDoc({
          reqBody: {
            //改活2
            // "username": "54188",
            // "password": "88888888",
            // "email": "hm2897135735@qq.com",
            // "phone": "18888888888",

            "username": this.name,
            "password": this.password,
            "email": this.email,
            "phone": this.phone,
            "verificationCodes": this.yanzhengma
          },
          reqUrl: "http://8.148.31.153:3030/signin",
          callback: (error, resData) => {
            try {
              if (!error) {
                const data = JSON.parse(resData)

                if (data.success) {
                  // const times1 = setTimeout(() => { this.$router.push({ name: "UserLogin" }) }, 2000)
                  // times1
                  //跳转登录
                  this.$store.commit("tanchaung", "注册成功")
                  this.$store.commit("tanchuangshow", true)
                  this.$router.push({ name: "UserLogin" })
                  this.isDisabled1 = false;
                } else {
                  //数据错误
                  this.$store.commit("Cuowu", data.message.msg == "用户已存在" ? "用户已存在" : "信息填写错误")
                  this.$store.commit("Cuowushow", true)
                  this.annuvalue = "注册"
                  this.isDisabled1 = false
                }
              }
            } catch (error) {
              console.log(error.messae)
            }
          }
        })
      } else {
        //不同意
        this.annuvalue = "注册"
        this.$store.commit("tanchaung", "同意协议方可登录")
        this.$store.commit("tanchuangshow", true)
        this.tongyi = true;
        this.isDisabled1 = false

      }
    },

    //name
    yhname() {
      if (/^[\u4e00-\u9fa5a-zA-Z0-9@._-]{1,14}$/.test(this.name)) {
        this.cwname = ""
        return;
      } else if (this.name == "") {
        (this.cwname = "用户名不能为空")
        return;
      } else if (/\s/.test(this.name)) {
        (this.cwname = "用户名不能有空格")
        return;
      } else {
        (this.cwname = "用户名不应该超过14个字");
        return;
      }
    },

    //phone
    yhphone() {
      /^1[3-9]\d{9}$/.test(this.phone) ? (
        this.cwphone = ""
      ) : this.phone === "" ? (
        (this.cwphone = "手机号不能为空")
      ) : (
        (this.cwphone = "请输入正确手机号")
      )
    },

    //password
    yhpassword() {
      /^.{8,16}$/.test(this.password)
        ? this.cwpassword = ""
        : this.password === ""
          ? (this.cwpassword = "密码不能为空")
          : (this.cwpassword = "密码应为8~16个字符");
    },

    //again  password
    yhagainpassword() {
      if (this.password == this.againpassword) {
        this.cwagainpassword = ""
      } else {
        (this.cwagainpassword = "第二次密码错误");
      }
    },

    //Email
    yhemail() {
      if (this.email === "") {
        this.cwemail = "电子邮件不能为空";
        this.isemail = false
        return;
      }
      if (/\s/.test(this.email)) {
        this.cwemail = "电子邮件不能有空格";
        this.isemail = false
        return;
      }
      if (!/^[a-zA-Z0-9\u4e00-\u9fa5._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,}$/.test(this.email)) {
        this.cwemail = "电子邮件格式不正确";
        this.isemail = false
        return;
      }
      if (this.email.length > 64) {
        this.cwemail = "电子邮件不应超过64个字符";
        this.isemail = false
        return;
      }
      this.cwemail = "";
      this.isemail = true
    },

    //输入验证码
    shuruyanzhengma() {
      if (!/^\d*$/.test(this.yanzhengma)) {
        this.yanzhengma = ''
        this.cwyanzhengma = "只能输入数字"
        return
      }
      if (this.yanzhengma.length > 6) {
        this.yanzhengma = ''
        this.cwyanzhengma = "验证码必须是6位数字"
      } else {
        this.cwyanzhengma = "";
      }
    },
    //同意
    iftongyi() {
      this.tongyi = !this.tongyi
    },

    // 协议
    xieyi() {
      this.$router.push({ name: "UserAgreement" })
    },

    lijidenglu() {
      this.$router.push("UserLogin");
    },
  },
};
</script>
<style>
:root {
  --primary-color: #3498db;
  --primarys-hover: #358ac3;
  --error-color: red;
  --border-color: #e0e0e0;
  --text-color: #333;
  --background-light: #f8f9fa;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  --transition: all 0.3s ease;
}

.content-page {
  width: 100%;
  margin: 0 auto;
}

.register-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: var(--card-shadow);
  overflow: hidden;
  width: 100%;
}

.page-header {
  background: var(--primary-color);
  color: white;
  text-align: center;
  padding: 30px;
}

.page-header h2 {
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 600;
}

.page-header p {
  font-size: 16px;
  opacity: 0.9;
}

.form-content {
  padding: 30px;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: -20px;
}

.form-col {
  flex: 1;
  min-width: 250px;
}

.form-group {
  margin-bottom: 5px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 15px;
  color: #555;
}

.form-control {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  font-size: 15px;
  transition: var(--transition);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.verification-group {
  display: flex;
  gap: 10px;
}

.verification-group .form-control {
  flex: 1;
}

.verification-code-btn {
  background-color: #f8f9fa;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 0 15px;
  cursor: pointer;
  color: var(--primary-color);
  font-weight: 500;
  white-space: nowrap;
  transition: var(--transition);
}

.verification-code-btn:hover {
  background-color: #e3f2fd;
}

.agreement-group {
  margin: 13px 0;
  display: flex;
  align-items: flex-start;
}

.agreement-group input {
  margin-top: 3px;
  margin-right: 8px;
}

.error-message {
  display: block;
  user-select: none;
  -webkit-user-select: none;
  pointer-events: none;
  caret-color: transparent;
  color: var(--error-color);
  font-size: 13px;
  margin-top: 6px;
  display: block;
  height: 14px;
}

.agreement-group a {
  color: var(--primary-color);
  text-decoration: none;
}

.btn {
  display: block;
  width: 100%;
  padding: 13px 20px;
  background-color: var(--primary-color);
  color: white;
  font-size: 16px;
  font-weight: 500;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: var(--transition);
}

.btn:hover {
  background-color: #358ac3;
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
  font-size: 15px;
}

.login-link a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  margin-left: 5px;
}
</style>