export default function loadXMLDoc({
  reqBody = {}, // {key:value}
  reqMethod = "post", // 请求方式
  reqUrl = "", // 请求目标地址
  async = true, // 是否异步
  reqHeader = { "Content-Type": "application/x-www-form-urlencoded" }, // {key:value}
  callback, // 回调函数
}) {
  let params = null;
  let reqBodyData = null;
  switch (Object.values(reqHeader)[0]) {
    case "application/x-www-form-urlencoded":
      params = new URLSearchParams();
      Object.entries(reqBody).forEach(([key, value]) => {
        params.append(key, value); // 添加所有的请求数据
      });
      reqBodyData = params.toString();
      break;
    case "application/json":
      params = JSON.stringify(reqBody);
      reqBodyData = params;
      break;
  }
  // 构建字符串
  let xhr = new XMLHttpRequest();
  xhr.open(reqMethod, reqUrl, async);
  Object.entries(reqHeader).forEach(([key, value]) => {
    xhr.setRequestHeader(key, value); // 处理请求头
  });

  xhr.onload = function () {
    if (xhr.status >= 200 && xhr.status < 300) {
      callback(null, xhr.response);
    } else {
      callback(new Error(`请求失败，状态码：${xhr.status}`));
    }
  };
  xhr.onerror = function () {
    callback(new Error("网络错误"));
  };
  xhr.send(reqBodyData);
}

